import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "Home",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/Home.vue"),
    },

    {
        path: "/profile",
        name: "Profile",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/Profile.vue"),
    },



    //PLanificdor-----------------------------

    {
        path: "/planner/plannings",
        name: "Planner plannings",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/planner/Plannings.vue"),
    },
    {
        path: "/planner/ots/:id_c",
        name: "Planner ot",
        component: () =>
            import (
                /* webpackChunkName: "about" */
                "../views/planner/Ot.vue"
            ),
    },
    {
        path: "/planner/pms",
        name: "PM",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/planner/Pm.vue"),
    },
    {
        path: "/planner/load-balancing",
        name: "LoadBalancing",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/planner/LoadBalancing.vue"),
    },
    //Supervisor-------------------------------------
    {
        path: "/supervisor/plannings",
        name: "All plannings",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/supervisor/Plannings.vue"),
    },
    {
        path: "/supervisor/my-plannings",
        name: "My plannings",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/supervisor/MyPlannings.vue"),
    },
    {
        path: "/supervisor/ot/:id_c",
        name: "Ot",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/supervisor/Ot.vue"),
    },
    {
        path: "/computada",
        name: "Computada",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/PropiedadesComputadas.vue"),
    },
    {
        path: "/supervisor/delivery/:a_name/",
        name: "delivery",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/supervisor/Delivery.vue"),
    },

    {
        path: "/supervisor/pm03",
        name: "pm03-supervisor",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/supervisor/Pm03.vue"),
    },
    {
        path: "/supervisor/selectUser",
        name: "selectUser",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/supervisor/selectUser.vue"),
    },
    {
        path: "/p",
        name: "p",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/technical/p.vue"),
    },



];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes,
});

export default router